@media #{$screen-sm} {
  .burger {
    display: none;
  }

  .row {
    display: flex;
  }

  .with-shadow {
    .header__topbar {
      box-shadow: 0 6px 11px 0 rgba(91, 89, 252, 0.1);         
    }
  }

  .header__nav-item + .btn,
  .btn + .btn {
    margin: 0;
    margin-left: 20px;
  }

  .header {
    &__column {
      width: 315px;  
      align-items: flex-start;    
    }

    &__icons {
      animation: none;
      left: 0;
    }

    &__image {
      position: relative;
      right: -30px;
      top: -50px;
      width: 460px;
      height: 460px;
    }

    &__main {
      align-items: flex-start;
      text-align: left;
    }
    
    &__nav-list {
      display: flex;
      align-items: center;

      & * {
        z-index: 1;
      }
    }

    &__nav-link {
      font-size: 16px;
    }

    &__nav-item {
      margin: 0;
      font-weight: 400;
      padding: 0 20px;
      
      &:first-of-type {
        margin-top: 0;
      }
    }


    &__row {
      display: flex;
      margin: 0 auto;
      margin-top: 120px;
    }

    &__text {
      padding: 0;
      line-height: 1.5;

      &--center {
        margin: 0 auto 20px;
        opacity: 0.6;
      }
    }
  }
}

@media #{$screen-only-sm-and-md} {
  .header-tab {
    &-left,
    &-right {
      display: block;
      position: absolute;
      z-index: 2;
    }

    &-left {
      height: 480px;
      top: 110px;
      left: -20px;
    }

    &-right {
      height: 440px;
      top: 120px;
      right: 30px;
    }
  }
}
